body {
  font-family: "Roboto", sans-serif;
}

body {
  background-color: #EDD685;
}

.link {
  text-decoration: none;
  color: white;
}
.link:hover {
  text-decoration: underline;
  font-weight: 600;
}

.nav-list {
  display: flex;
  flex-direction: column;
  padding: 0;
  list-style: none;
}
.nav-list__item {
  margin-left: 0.5rem;
}
.nav-list__item:first-child {
  margin-left: 0;
}
.nav-list--horizontal {
  flex-direction: row;
}

.button {
  display: block;
  height: min-content;
  text-decoration: none;
  font-weight: normal;
  text-align: center;
  color: white;
  padding: 0.25rem 0.5rem;
  background-color: gray;
  border-radius: 5%;
  border-style: none;
  cursor: pointer;
}
.button[type=button] {
  border: none;
  cursor: pointer;
  outline: none;
}
.button--primary {
  background-color: #74A793;
  color: white;
}
.button--primary:hover {
  background-color: #5a8e7a;
}
.button--outline-primary {
  background-color: white;
  color: #74A793;
}
.button--outline-primary:hover {
  background-color: #e6e6e6;
}
.button--secondary {
  background-color: #987627;
  color: white;
}
.button--secondary:hover {
  background-color: #6f561d;
}
.button--outline-secondary {
  background-color: white;
  color: #987627;
}
.button--outline-secondary:hover {
  background-color: #e6e6e6;
}
.button--edit {
  background-color: #987627;
  color: white;
  border-radius: initial;
}
.button--edit:hover {
  background-color: #6f561d;
}
.button--outline-edit {
  background-color: white;
  color: #987627;
  border-radius: initial;
}
.button--outline-edit:hover {
  background-color: #e6e6e6;
}
.button--view {
  background-color: #CF8655;
  color: white;
  border-radius: initial;
}
.button--view:hover {
  background-color: #bc6b35;
}
.button--outline-view {
  background-color: white;
  color: #CF8655;
  border-radius: initial;
}
.button--outline-view:hover {
  background-color: #e6e6e6;
}
.button--delete {
  background-color: #DC3545;
  color: white;
  border-radius: initial;
}
.button--delete:hover {
  background-color: #bd2130;
}
.button--outline-delete {
  background-color: white;
  color: #DC3545;
  border-radius: initial;
}
.button--outline-delete:hover {
  background-color: #e6e6e6;
}

.grid--modal {
  grid-template-columns: 1fr;
  grid-template-rows: repeat(2, min-content);
  max-width: 50ch;
}
.grid--modal > * {
  margin: 0 0 0.5rem 0;
}

.modal:not([open]) {
  display: none;
}
.modal__title {
  justify-self: center;
  grid-column: 1;
  grid-row: 1/2;
}
.modal__action {
  grid-column: 1;
  grid-row: 2/3;
}

.grid--form {
  grid-template-areas: "fields" "button";
  grid-template-columns: 1fr;
  grid-template-rows: 1fr min-content;
}

.form {
  border: 3px solid black;
  min-height: 50vh;
  width: 50vw;
  padding: 1rem 1rem 0;
}
.form__button {
  grid-area: button;
  align-self: end;
  margin-bottom: 1rem !important;
}
.form-fields {
  grid-area: fields;
}
.form-group {
  display: flex;
  flex-direction: row;
  margin-bottom: 0.5rem;
}
.form-group__label {
  display: flex;
  align-items: center;
  padding: 0.25rem;
  background-color: #C4C4C4;
  border-color: black;
  border-width: 3px;
  border-style: solid none solid solid;
  white-space: nowrap;
}
.form-group__input {
  width: 100%;
  outline: none;
  border: 3px solid black;
  border-radius: unset;
}
.form-group__input--textarea {
  resize: vertical;
  min-height: 4rem;
}
.form-group:last-child {
  margin-bottom: 0;
}

.nav {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-template-rows: 1fr;
  background-color: #1E2828;
  width: 100%;
  height: 3.5rem;
}
.nav__left {
  display: flex;
  height: 100%;
  grid-column: 1/2;
  grid-row: 1;
  align-items: center;
}
.nav__left .button--nav {
  margin-left: 0.5rem;
}
.nav__center {
  display: flex;
  height: 100%;
  justify-content: center;
  grid-column: 2/3;
  grid-row: 1;
  align-items: center;
}
.nav__right {
  display: flex;
  height: 100%;
  flex-direction: row;
  grid-column: 3/4;
  align-items: center;
  grid-row: 1;
  justify-content: flex-end;
}
.nav__right .button--nav {
  margin-left: 0.5rem;
}
.nav__right .button--nav:first-child {
  margin-left: 0;
}
.nav__right .button--nav:last-child {
  margin-right: 0.5rem;
}

.grid {
  display: grid;
  grid-template-columns: auto;
  grid-template-rows: auto;
}

.content {
  padding: 0 1rem 1rem 1rem;
  grid-template-rows: min-content 1fr;
  grid-template-columns: 1fr;
}
.content__title {
  grid-row: 1/span 1;
  grid-column: 1;
  justify-self: center;
}
.content__items {
  grid-row: 2/span 2;
  grid-column: 1;
}

.grid--items {
  grid-template-columns: repeat(auto-fit, minmax(15rem, min-content));
  grid-gap: 1rem;
  justify-content: center;
}

.grid--item {
  display: grid;
  grid-template-areas: "title title title" "body body body" "timestamp timestamp timestamp" "separator separator separator" "view edit delete";
  grid-template-columns: repeat(3, 1fr);
  grid-template-rows: min-content 1fr repeat(3, min-content);
  background-color: #C4C4C4;
  height: 11rem;
  padding: 0.5rem;
}
.grid--item__title {
  grid-area: title;
  justify-self: center;
  margin: 0 0.25rem;
}
.grid--item__body {
  overflow: hidden;
  grid-area: body;
  margin: 0;
}
.grid--item__timestamp {
  grid-area: timestamp;
  color: #616868;
}
.grid--item__timestamp__label {
  font-weight: bold;
}
.grid--item__timestamp__time {
  display: inline;
}
.grid--item__view {
  grid-area: view;
}
.grid--item__separator {
  width: 100%;
  grid-area: separator;
  height: 0.15rem;
  border-width: 0;
  background-color: #9e9e9e;
}
.grid--item__edit {
  grid-area: edit;
  margin-left: 0.5rem;
}
.grid--item__delete {
  grid-area: delete;
  margin-left: 0.5rem;
}

.grid--introduction {
  grid-template-areas: "title" "image" "description";
  grid-template-columns: 1fr;
  grid-template-rows: repeat(3, auto);
}

.introduction__title {
  grid-area: title;
  justify-self: center;
}
.introduction__img {
  grid-area: image;
  width: 25%;
  justify-self: center;
}
.introduction__about-me {
  grid-area: description;
  width: 65%;
  justify-self: center;
}

.grid--site-form {
  grid-template-columns: 1fr;
  grid-template-rows: min-content 1fr min-content;
  grid-template-areas: "title" "body" "button";
  min-height: calc(100vh - 3.5rem);
}

.site-form {
  padding: 0 0.5rem;
}
.site-form__title {
  grid-area: title;
  justify-self: center;
}
.site-form__content {
  grid-area: body;
  justify-self: center;
  margin: 0;
}
.site-form__content--view {
  max-width: 85ch;
  border: 3px solid black;
  padding: 0.5rem;
  background-color: rgba(196, 196, 196, 0.5);
}
.site-form__content--create-edit .form {
  background-color: rgba(196, 196, 196, 0.5);
}
.site-form__action-btn {
  grid-area: button;
  width: min-content;
  margin-bottom: 0.5rem;
}
.site-form__action-btn--save {
  margin-top: 0.5rem;
  justify-self: end;
}